import React, {useState, useEffect}from "react";
import { useWeb3React } from '@web3-react/core';
import classes from "./Dome.module.css";




const Web3ReactConnectionComponent = () => {
	//connector, library, chainId, account, activate, deactivate
	const web3reactContext = useWeb3React(); 
	 const [balance,setBalance]= useState("")
	 const {account,library} = useWeb3React();
	 
	 
	 
	 //useEffect balance
	  useEffect(() => {
    library?.getBalance(account).then((result)=>{
      setBalance(result/1e18)
    })
    },);
	
	
	


 

	const disconnectMetamaskSimple = () => {
		try {
			web3reactContext.deactivate();
		} catch (ex) {
			console.log(ex);
		}
	};

	

	
const Card = (props) => {
  return (
    <div className={`${classes.card} ${props.className}`}>{props.children}</div>
  );
};
	

	return (
		<Card className={classes.dome}>
			<h2>&#9884; Connect Panel&#9884;</h2>	<br />	
				{web3reactContext.account ? <h2>Account: {web3reactContext.account}</h2> : <p>Not connected</p>}
				{web3reactContext.account ? <h2> Balance: {balance} ETH  </h2> : <p> </p>}
			<div className="flex flex-col space-y-2 items-start pt-10  ">
				

					
				<button
						className="bg-red-700 hover:bg-green-700 text-white font-bold py-2 px-4 rounded "
						onClick={disconnectMetamaskSimple}
					>
						Disconnect
				</button>
		  </div>
		</Card>
	);
};
export default Web3ReactConnectionComponent;
